import React, { useState } from 'react'

const API_FETCH = 'https://thingproxy.freeboard.io/fetch/'
const ENDPOINT = API_FETCH + 'http://gucitex.com.mx/test'
//const ENDPOINT = 'http://192.168.0.27/react_php-cuidatusventas/backend'
const TOKEN_DEFAULT = '0eab84ea-917d-4817-9e33-c98caecf626d';

export const Context = React.createContext()

export const ContextProvider = ({ children }) => {

  //--------------- STATES --------------------//
  const [isOpen, setIsOpen] = useState(false)

  //--------------- FUNCTIONS -----------------//
  async function processResponse(pro) {
    //console.log('promise:', pro)

    let data = await pro
      .then(res => res.text())
      .then(txt => {
        console.log('resTxt:', txt)
        return JSON.parse(txt)
      })
      .catch(error => {
        //console.log('error fetch:', error)
        alert('ERROR DE COMUNICACION CON EL SERVIDOR')
        return null
      })

    console.log('data before return:', data)

    if (data !== null) {
      //data['isAuth'] = false

      if (data['isAuth']) delete data['isAuth']
      else {
        console.log('CLOSE SESSION')
        data = null
      }
    }

    return data
  }

  const onFetch = async (params, method, token = TOKEN_DEFAULT, body = null) => {
    console.log('endpoint:', ENDPOINT + params)
    const pro = fetch(ENDPOINT + params, {
      method: method,
      headers: {
        'Content-Type': 'application/json',
        Authorization: token,
        Origin: 'web-cuidatusventas'
      },
      mode: 'cors',
      body: body
    })
    return await processResponse(pro)
  }  

  return (
    <Context.Provider value={{
      onFetch,
      isOpen,
      setIsOpen
    }}>
      {children}
    </Context.Provider>
  )
}